html {
    height: 100%;
}

body {
    margin:0;
    height:100%;
    background-color: #f0f0f0;
}

.container {
    display:flex; 
    height: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content-avatar {
    position: relative;
    bottom: -95px;
    border: #f0f0f0 solid 20px;
    border-radius: 50%;
    background-color: white;
    z-index: 0;

    &::before{
        animation: pulse 2s linear infinite;
        border: #ffffff solid 20px;
        border-radius: 50%;
        box-sizing: border-box;
        content: ' ';
        height: 140%;
        left: -20%;
        opacity: .9;
        position: absolute; 
        top: -20%;
        transform: scale(0.7);
        width: 140%;
        z-index: -1;
    }
}

.content-avatar img{
    z-index: 2;
    border-radius: 50%;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.content-box {
    padding-top: 90px;
    position: relative;
    text-align: center;
    background-color: white;
    font-family: 'Fira Sans';
    width:100%;
    height:100%;
    border-radius: 2px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    z-index: -1;
}

/*
    Colour schemes:
    white & #f0f0f0
    
*/
@keyframes pulse {
    to {
      opacity: 0;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
